<script>
	export default {
		data: function() {
			return {
				wallet_html: "Connect wallet",
				dialogTableVisible:false
			}
		},
		created() {
			this.wallet.created()
			var temp = this
			setInterval(function() {
				if(typeof window.ethereum !== 'undefined' && ethereum.selectedAddress && ethereum.selectedAddress.length==42){
					temp.wallet.address = ethereum.selectedAddress
				}
				if(temp.wallet.address.length==42){
					temp.wallet_html = temp.wallet.address
				}else{
					temp.wallet_html = "Connect wallet"
				}
			}, 1000);
		},
		methods: {
			wallet_click() {
				if (this.wallet.address.length == 42) {
					if (this.wallet.M_connector.connected) {
						this.wallet.M_connector.killSession()
					}
					this.wallet.address = ""
					ethereum.selectedAddress = ""
				} else {
					this.dialogTableVisible = true
				}
			},
			connectWalletConnect_click() {
				this.dialogTableVisible = false
				this.wallet.connectWallet()
			},
			connectMetamask_click() {
				var temp = this
				this.dialogTableVisible = false
				if (typeof window.ethereum !== 'undefined') {
					console.log('MetaMask is installed!');
					ethereum.request({
						method: "wallet_requestPermissions",
						params: [{
							eth_accounts: {}
						}]
					}).then((result) => {
						console.log(ethereum)
					}).catch((error) => {
						temp.$message({
							message: 'User cancel authorization'
						});
					});
				} else {
					temp.$message({
						message: 'You need to install metamask'
					});
				}
			}
		}
	}
</script>

