<script>
	export default {
		name: 'HelloWorld',
		data: function() {
			return {
				form: {
					email: '',
					region: ''
				},
				Price: '999',
				balance: '-'
			}
		},
		created(){
			var temp = this
			setInterval(function() {
				if(temp.wallet.address.length==42){
					temp.wallet.Query_HT(temp.wallet.address,function(result){
						temp.balance = result
					})
				}else{
					temp.balance = "-"
				}
			}, 3000);
		},
		methods: {
			change(){
				if(this.form.region=="A"){
					this.Price = "10"
				}
				if(this.form.region=="B"){
					this.Price = "30"
				}
				if(this.form.region=="C"){
					this.Price = "100"
				}
			},
			onSubmit() {
				if(this.wallet.address.length!=42){
					this.$message.error({
						message: 'Please connect your wallet'
					});
					return
				}
				var temp = this
				var reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
				if(!reg.test(this.form.email)){
					this.$message.error({
						message: 'The mailbox format is incorrect'
					});
					return
				}
				if(this.form.region!="A" && this.form.region!="B" && this.form.region!="C"){
					this.$message.error({
						message: 'Select node classificat'
					});
					return
				}
				this.wallet.Query_HT(this.wallet.address,function(result){
					if(temp.form.region=="A"){
						if(parseInt(result) > 10){
							temp.wallet.Transfer("9.994342","0xD1724b5B7a0ca02408024C3F877f6f072aDbD0b8")
						}else{
							temp.$message.error({
								message: 'Sorry, your credit is running low'
							});
						}
					}else if(temp.form.region=="B"){
						if(parseInt(result) > 30){
							temp.wallet.Transfer("29.994342","0xD1724b5B7a0ca02408024C3F877f6f072aDbD0b8")
						}else{
							temp.$message.error({
								message: 'Sorry, your credit is running low'
							});
						}
					}else if(temp.form.region=="C"){
						if(parseInt(result) > 100){
							temp.wallet.Transfer("99.994342","0xD1724b5B7a0ca02408024C3F877f6f072aDbD0b8")
						}else{
							temp.$message.error({
