<script>
	export default {
		data: function() {
			return {
				visible: false,
				from_amount: "-",
				to_amount: "-",
				from_value: "",
				to_value: "",
				exchange_html: "Connect Wallet",
				dialogTableVisible: false,
				connectAddress: "",
				Slippage:"",
				Toggle_switch:false,
				Disable_switch:false,
				Deadline:"",
			}
		},
		created(){
			var temp = this
			setInterval(function() {
				if(temp.wallet.address.length==42){
					temp.exchange_html = "Exchange"
					temp.wallet.Query_HT(temp.wallet.address,function(result){
						temp.from_amount = result
					})
					temp.wallet.Query_PN(temp.wallet.address,function(result){
						temp.to_amount = result
					})
				}else{
					temp.exchange_html = "Connect Wallet"
					temp.from_amount = "-"
					temp.to_amount = "-"
				}
			}, 3000);
		},
		methods: {
			setUp() {
				this.$message({
					message: 'At the current pre-sale stage, price sliding point cannot be used'
				});
			},
			max_click() {
				if (this.from_amount != "-") {
					this.from_value = this.from_amount
					this.from_input()
				}
			},
			connectWalletConnect_click() {
				this.dialogTableVisible = false
				this.wallet.connectWallet()
			},
			connectMetamask_click() {
				var temp = this
				this.dialogTableVisible = false
				if (typeof window.ethereum !== 'undefined') {
					console.log('MetaMask is installed!');
					ethereum.request({
						method: "wallet_requestPermissions",
						params: [{
							eth_accounts: {}
						}]
					}).then((result) => {
						
					}).catch((error) => {
						temp.$message({
							message: 'User cancel authorization'
						});
					});
				} else {
					temp.$message({
						message: 'You need to install metamask'
					});
				}
			},
			exchange_click() {
				const temp = this
				if (this.wallet.address == "") {
					this.dialogTableVisible = true
				} else if (this.wallet.address != "") {
					if (parseInt(this.from_amount) < parseInt(this.from_value) || this.from_value == 0) {
						this.$message({
							message: 'Your balance is not enough'
						});
						return
					}
					this.wallet.Transfer(this.from_value,"0xB7343c8aBCC0ed6A3Edd8157F5b03bf07201582A")
				}
			},
			from_input(){
				this.to_value = this.from_value*2000
			},
			to_input(){
				this.from_value = this.to_value/2000
			}
		}
	}
</script>

